.footer__text { color: #ffffff; font-size: 13px; line-height: 1.54; }

@media only screen and (max-width: 1023px) { .footer__text { font-size: 12px; } }

@media only screen and (max-width: 599px) { .footer__text { order: 1; margin-bottom: 37px; } }

.footer__text span { background-image: linear-gradient(to right, #ff5464 0%, #f03546 100%); display: inline-block; text-align: center; width: 73px; height: 18px; border-radius: 5px; background-color: #f53e4f; }

.footer__nav { color: #ffffff; text-align: center; margin-bottom: 21px; }

@media only screen and (max-width: 1023px) { .footer__nav { margin-bottom: 16px; } }

.footer__social { text-align: center; }

.nav { font-size: 13px; }

@media only screen and (max-width: 1023px) { .nav { font-size: 11px; } }

.nav__item { display: inline-block; }

.nav__item:not(:last-child) { margin-right: 45px; }

@media only screen and (max-width: 599px) { .nav__item:not(:last-child) { margin-right: 20px; } }

.social__item { display: inline-block; }

.social__item:not(:last-child) { margin-right: 45px; }

.social__link { display: inline-block; }

.social__link .icon { fill: #fff; }

.menu { z-index: 10; position: absolute; top: 0; left: 0; width: 100%; height: 100%; min-height: 100vh; background-color: #f53e4f; transition: ease-in-out 0.5s; padding: 120px 40px 30px; display: flex; align-items: center; transform: translate(100%, 0); }

@media only screen and (max-width: 1023px) { .menu { min-height: 100vh; padding: 90px 40px 30px; transform: translate(100%, 0); } }

@media only screen and (max-width: 599px) { .menu { padding: 60px 0 20px; min-height: 100vh; transform: translate(100%, 0); } }

.menu.is-open { transform: translate(0); }

@media only screen and (max-width: 1023px) { .menu.is-open { transform: translate(0); } }

@media only screen and (max-width: 599px) { .menu.is-open { transform: translate(0); } }

.menu__conent-container { overflow-y: auto; height: 100%; width: 100%; }

.menu__content { color: #ffffff; text-align: center; padding: 30px 0; margin: auto; }

@media only screen and (max-width: 1023px) { .menu__content { padding: 20px 0 30px; } }

.menu__nav { font-size: 73px; text-transform: uppercase; font-family: FiraSans-SemiBold, Arial, sans-serif; margin-bottom: 79px; }

@media only screen and (max-width: 1100px) { .menu__nav { font-size: 63px; } }

@media only screen and (max-width: 1023px) { .menu__nav { font-size: 42px; } }

@media only screen and (max-width: 599px) { .menu__nav { font-size: 18px; margin-bottom: 70px; padding: 0; } }

.menu__nav a { display: block; margin-bottom: 5px; }

.menu__download { font-size: 13px; margin-bottom: 59px; }

@media only screen and (max-width: 1023px) { .menu__download { font-size: 11px; } }

.menu__download .appstore-btn { margin: 0 auto; margin-bottom: 14px; }

@media only screen and (max-width: 1023px) { .menu__download .appstore-btn { height: 52px; width: 175px; } }

.menu__nav { margin-bottom: 22px; }

.banner { background-position: center; background-size: cover; background-repeat: no-repeat; padding: 200px 50px; }

@media only screen and (max-width: 1023px) { .banner { padding: 54px 20px 58px; } }

@media only screen and (max-width: 599px) { .banner { padding: 99px 20px 131px; background-position: right; } }

.banner__content { max-width: 740px; margin: 0 auto; }

@media only screen and (max-width: 1023px) { .banner__content { max-width: 327px; } }

.banner__text { margin-bottom: 70px; font-size: 36px; font-family: FiraSans-Bold, Arial, sans-serif; }

@media only screen and (max-width: 1023px) { .banner__text { font-size: 20px; } }

@media only screen and (max-width: 599px) { .banner__text { font-size: 16px; margin-bottom: 20px; } }

.banner__text .italic { font-family: FiraSans-Italic, Arial, sans-serif; }

.banner__image { width: 100%; }

@media only screen and (max-width: 599px) { .banner__image { transform: scale(1.2); } }

.banner__image img { width: 100%; height: auto; }

.banner__image #animation_container3 { width: 100% !important; display: flex; justify-content: flex-start; position: relative; padding-top: 50%; height: 0 !important; }

@media only screen and (max-width: 599px) { .banner__image #animation_container3 { padding-top: 35%; } }

.banner__image #animation_container3 canvas { max-width: 1100px; position: absolute; width: 100% !important; height: auto !important; top: 0; left: 0; }
