.menu {
	z-index: 10;
	@include coverdiv;
	//min-height: calc(100vh - 140px)
	min-height: 100vh;
	background-color: #f53e4f;
	//overflow-y: auto
	transition: ease-in-out 0.5s;
	padding: 120px 40px 30px;
	display: flex;
	align-items: center;
	//transform: translate(100%, 140px)
	transform: translate(100%, 0);
	@include md {
		//min-height: calc(100vh - 116px)
		min-height: 100vh;
		padding: 90px 40px 30px;
		//transform: translate(100%, 116px)
		transform: translate(100%, 0); }
	@include xs {
		padding: 60px 0 20px;
		min-height: 100vh;
		//min-height: calc(100vh - 56px)
		transform: translate(100%, 0);
 }		//transform: translate(100%, 56px)
	&.is-open {
		transform: translate(0);
		//transform: translate(0, 140px)
		@include md {
			transform: translate(0);
 }			//transform: translate(0, 116px)
		@include xs {
			transform: translate(0);
 } }			//transform: translate(0, 56px)
	&__conent-container {
		overflow-y: auto;
		height: 100%;
		width: 100%; }
	//	position: fixed
	//	top: 0
	//	left: 0
	//	width: 100%
	//	height: 120px
	//	background-color: #f53e4f
	&__content {
		//max-width: 875px
		color: #ffffff;
		text-align: center;
		padding: 30px 0;
		margin: auto;
		@include md {
			padding: 20px 0 30px; } }
	&__nav {
		font-size: 73px;
		text-transform: uppercase;
		@include semi;
		margin-bottom: 79px;
		@include r(1100) {
			font-size: 63px; }
		@include md {
			font-size: 42px; }
		@include xs {
			font-size: 18px;
			margin-bottom: 70px;
			padding: 0; }
		a {
			display: block;
			margin-bottom: 5px; } }
	&__download {
		font-size: 13px;
		margin-bottom: 59px;
		@include md {
			font-size: 11px; }
		.appstore-btn {
			margin: 0 auto;
			margin-bottom: 14px;
			@include md {
				height: 52px;
				width: 175px; } } }
	&__nav {
		margin-bottom: 22px; } }

